.rightbar{
  flex: 3.5;
}

.rightbarWrapper{
  padding: 20px 20px 0 0;
  margin-left: 5px;
}

.birthdayContainer{
  display: flex;
  align-items: center;
}

.birthdayImg{
  width: 40px;
  height: 40px;
  margin-right: 10px;
  margin-left: 5px;
}

.rightbarAd{
  width: 100%;
  margin: 30px 0 30px 10px;
  border-radius: 10px;
}

.rightbarTitle{
  margin-bottom: 15px;
}

.rightbarFriendList{
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightbarProfile{
  margin-left: 5px;
}

.rightbarTitle{
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}

.rightbarInfo{
  margin-bottom: 30px;
}

.rightbarInfoItem{
  margin-bottom: 10px;
}

.rightbarInfoKey{
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}

.rightbarInfoValue{
  font-weight: 300;
}

.rightbarFollowings{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.rightbarFollowing{
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;
}

.rightbarFollowingImg{
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  
}