.topbarContainer{
  height: 50px;
  width: 100%;
  background-color: #1877f2;  
  display: flex;
  align-items: center;
  position: sticky;
   top:0;
   z-index: 999;
}

.topbarLeft{

  flex: 3;
}

.logo{
  font-size: 24px;
  margin-left: 20px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.topbarCenter{

  flex: 5;
}

.searchbar{
  width: 100%;
  height: 30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

.searchIcon{
  font-size: 20px !important;
  margin-left: 10px;
}

.searchInput{
   border: none;
   width: 70%;
}

.searchInput:focus{
  outline: none;
}


.topbarRight{

 flex: 4;
 display: flex;
 align-items: center;
 justify-content: space-around;
 color: white;
}

.topbarLink{

  font-size: 14px;
  margin-right: 10px;
  cursor: pointer;
}

.topbarIcons{

  display: flex;
}

.topbarIconItem{
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge{
  width: 15px;
  height: 15px;
  background-color: red;
  color: white;
  border-radius: 50%;
  position:absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.topbarImg{
  width: 32px;
  height: 32px;
  object-fit: cover;
  border-radius: 50%;
  cursor: pointer;
}